import { getReviewFromUrlandSetting } from "./oberlo/modules/getReview";
import { initBulkImport, initHandleImport } from './ImportFromAliorders/actionsImport/index';

/**
 * @getContent
 * Descript: Lưu reviews từ Modal backend Alireviews gửi lên
 */
function getContent(aliexpress = "", settings, isEmbedApp = false) {
  console.log("settings parsed: ", settings);
  const { get_only_picture = [], get_only_content = [] } = settings;
  let _get_only_picture, _get_only_content;
  if (typeof get_only_picture === "object") {
    if (get_only_picture.length === 1) {
      const value = get_only_picture.pop();
      _get_only_picture = value == "true" || value == true ? true : false;
    } else {
      _get_only_picture = "all";
    }
  } else {
    _get_only_picture = "all";
  }
  if (typeof get_only_content === "object") {
    if (get_only_content.length == 1) {
      const value = get_only_content.pop();
      _get_only_content = value == "true" || value == true ? true : false;
    } else {
      _get_only_content = "all";
    }
  } else {
    _get_only_content = "all";
  }

  settings.get_only_picture = _get_only_picture;
  settings.get_only_content = _get_only_content;
  console.log("serilized settings: ", settings);
  console.log("aliexpress link: ", aliexpress);

  !aliexpress && `javascript:GenerateReview.handleGetReviewExtension({argsReviews : ${JSON.stringify(
      []
  )}})`;

  aliexpress && getReviewFromUrlandSetting(aliexpress, settings).then(result => {
    const reviews = JSON.stringify(result);
    if (isEmbedApp) {
      if (reviews && reviews.length) {
        sendMessageToEmbedApp({
          type_message: "arlvGetReviewExtensionEmbedApp",
          reviews,
          settings_from_ext: settings,
          review_link: aliexpress
        })
        return;
      }
      console.warn("no review");
    } else {
      reviews &&
        (window.location.href = `javascript:GenerateReview.handleGetReviewExtension({argsReviews : ${reviews}})`);
      !reviews && console.warn("no review");
    }
  });
}
// END: @getContent

/**
 * @Import reviews from Aliorders
 * Descript: Thêm 2 chức năng HandleImport / BulkImport  review product cho trang Aliorders
 */
document.addEventListener("DOMContentLoaded", function() {
  const ProductPage = document.getElementById('page-products');
  if( ProductPage ) {
    document.head.insertAdjacentHTML("beforeend", `<link href="${CSSALIORDERSPAGE}" rel="stylesheet">`);
  }
});

window.addEventListener("load", function () {
  const ProductPage = document.getElementById('page-products');
  if (ProductPage) {
    handleImportFromAliOrders()
  }
}, false);

function handleImportFromAliOrders() {
  let timer = null
  let countTimer = 1
  timer = setInterval(function() {
    countTimer += 1
    // const ProductPage = document.getElementById('page-products');
    let productListItem = $('.product-list-wrapper').find('.pricing-rule-block-count');

    if( productListItem.length > 0 ) {
      clearInterval(timer)
      const el = document.querySelector('.import-all-review-btn');
      el && el.remove();
      
      initBulkImport();
      initHandleImport();
    }
    if(countTimer >= 20){
      clearInterval(timer)
    }
  }, 500)
}
// END: @Import reviews from Aliorders

/** 
 * @Sidebar "Import from Oberlo"
 * Backend xóa href <a> Import from reviews
 * Kiểm tra Backend và Oberlo có sử dụng chung SHOP không
*/
window.addEventListener("load", function() {
  const elIsOberloLoginCurrentShop = document.getElementById('isOberloLoginCurrentShop')
  
  if(elIsOberloLoginCurrentShop) {
    elIsOberloLoginCurrentShop.removeAttribute('href')
  };
}, false);

function isChekOberloLoginCurrentShop(shop) {
  const urlOberloProduct = "https://app.oberlo.com/products";
  
  window.fetch(urlOberloProduct, {
    method: "GET",
    credentials: "include",
    redirect: "follow"
  })
  .then(function(response) {
    return response.text();
  })
  .then(function(sourceTextHML) {
    const regex = /window.App.payload.currentShopDomain = (.*?);$/m
    const currentShopdomainOnOberlo = regex.exec(sourceTextHML) === null ? "" : regex.exec(sourceTextHML)[1].replace(/\"/g, '');

    window.postMessage(
      {
        type_message: "isChekOberloLoginCurrentShop_result",
        isSameShopdomain: currentShopdomainOnOberlo == shop,
        urlOberloProduct: urlOberloProduct
      },
      "*"
    );
  });
}
// END: @Sidebar "Import from Oberlo"

/** 
 * @Sidebar "Import from Aliorders"
 * Backend xóa href <a> Import from aliorders
 * Kiểm tra Backend và Aliorder có sử dụng chung SHOP không
*/
window.addEventListener("load", function() {
  const elIsAliordersLoginCurrentShop = document.getElementById('isAliordersLoginCurrentShop')
  
  if(elIsAliordersLoginCurrentShop) {
    elIsAliordersLoginCurrentShop.removeAttribute('href')
  };
}, false);

function isChekAliordersLoginCurrentShop(shop) {
  const urlAliordersProduct = `${ALIORDERS}/products`;

  window.fetch(urlAliordersProduct, {
    method: "GET",
    credentials: "include",
    redirect: "follow"
  })
  .then(function(response) {
    return response.text();
  })
  .then(function(sourceTextHML) {
    var parser = new DOMParser();
    const doc = parser.parseFromString(sourceTextHML, "text/html");
    const elPageProducts = doc.getElementById('page-products');
    let currentShopdomainOnAliorders = elPageProducts === undefined ? "" : (doc.querySelector('.go-store-link') ? doc.querySelector('.go-store-link').getAttribute('href') : "");
    
    if(currentShopdomainOnAliorders) {
      const url = new URL(currentShopdomainOnAliorders);
      currentShopdomainOnAliorders = url.hostname;
    }
    
    window.postMessage(
      {
        type_message: "isChekAliordersLoginCurrentShop_result",
        isSameShopdomain: currentShopdomainOnAliorders == shop,
        urlAliordersProduct: urlAliordersProduct
      },
      "*"
    );
  });
}
// END: @Sidebar "Import from Aliorders"

export function sendMessageToEmbedApp(message) {
  const ifames = document.querySelectorAll('iframe');
  ifames.forEach(function(ifame) {
    if (ifame.src && ifame.src.includes(EMBED_APP_URL)) {
      ifame.contentWindow.postMessage(
        message,
        "*"
      );
      return;
    }
  });
}

function handleMessageFromAR(event) {
  const {
    data: { 
      type_message = "", 
      aliExpress, 
      settings = "", 
      shop = "",
      isEmbedApp = false,
    }
  } = event;

  type_message == "aliReview" && getContent(aliExpress, settings, isEmbedApp);
  type_message == "isChekOberloLoginCurrentShop" && isChekOberloLoginCurrentShop(shop);
  type_message == "isChekAliordersLoginCurrentShop" && isChekAliordersLoginCurrentShop(shop);
}

window.addEventListener("message", handleMessageFromAR, false);


