export function formatSpecialCharacter(string) {
  return string.replace(/([!@#$%^&*()+=\[\]\\';,./{}|":<>?~_-])/g, "\\$1");
}

export function convertStyleToStar(str) {
  const regex = /\d+/g;
  const exec = regex.exec(str);
  if (exec === null) {
    return 0;
  }
  return exec[0] / 20;
}

function extractShopifyDomain(el) {
  const attrHref = el.getAttribute("href");
  const url = new URL(attrHref);
  const { hostname } = url;
  return hostname;
}

export function getShopifyDomain() {
  const settingsDomain = getSettingsDomain();
  return settingsDomain.shop;
}

export function getSettingsDomain() {
  const shopDomain = document.querySelector('.go-store-link');
  
  let settingDomain;
  
  if(shopDomain) {
    const { hostname } = new URL(shopDomain.getAttribute('href'));
    
    settingDomain = {
      "alireviews": "alireviews.fireapps.io",
      "shop": hostname // Shopdomain
    }

  } else {
    //
    settingDomain = {
      "alireviews": "alireviews.fireapps.io",
      "shop": '' // Shopdomain
    }
  }

  return settingDomain;
}

/*
  @isPermitAccount
  return boolean
*/
export async function isPermitAccount() {
  const shop = getShopifyDomain();
  const shopSetting = await Api.getSetting(shop);

  if (shopSetting.status === "error") {
    const { message = "", url = "" } = shopSetting;
    const msg = `${message} <a href="${url}" target="_blank">Upgrade</a>`;

    toast(msg, "warning");
    return false;
  } 
    
  return true;
}

export function addAlireviewModalWrapper() {
  const div = document.createElement("div");
  div.classList.add("alireview-modal-wrapper");
  document.body.appendChild(div);
}

export function findProductId(el) {
  try {
    const shop = getShopifyDomain();
    const linkProduct = el.find(`a[href^="https://${shop}"]`);
    if (!linkProduct) {
      toast(chrome.i18n.getMessage("e02"));
      throw new Error(chrome.i18n.getMessage("e02"));
    }
    const productLink = linkProduct.attr("href");
    const url = new URL(productLink);
    const { pathname } = url;
    const productId = pathname.replace("/admin/products/", "");
    return productId;
  } catch (error) {
    toast(error.message);
    throw error;
  }
}

export function addAlireviewWrapper() {
  document.body.classList.add(alireviewWrapper);
}

