// * * * PAGE NEW * * *

export async function handleAutoBulkImportPageNew() {
    let isProcessBulk = false

    const oberloMainContent = document.querySelector(".page-layout__body-content");
// Position add Button Bulk Action on Header
    const headNav = document.querySelector(".page-layout__header .main-layout-header h2");

    oberloMainContent.insertAdjacentHTML(
        "afterbegin",
        '<div id="alireviews-bulk-importer"></div>'
    );

    headNav.insertAdjacentHTML(
        "afterend",
        '<li id="alireviews-bulk-nav-button"></li>'
    );
    const alireviewsBulkImporter = document.querySelector(
        "#alireviews-bulk-importer"
    );
    const alireviewsBulkNavButton = document.querySelector(
        "#alireviews-bulk-nav-button"
    );
    alireviewsBulkImporter.innerHTML = bulkImportHeroView({origin: ORIGIN});
    alireviewsBulkNavButton.innerHTML = bulkNavButton({origin: ORIGIN});

    alireviewsBulkImporter.insertAdjacentHTML("afterend", bulkImportModalView({origin: ORIGIN}));

    document.body.insertAdjacentHTML("beforeend", progressView());

    let radioImport = null
    $("#modalBulkImport").on("show.bs.modal", async () => {
        radioImport = await localStorage.getItem("radioImport");
        console.log("BULK ACTION - GET REVIEWS: [1/] Show modal #modalBulkImport", radioImport)
        $(`.group-checkbox input[value="${radioImport}"]`).attr("checked", true);
        if(radioImport == 'none'){
            $('input[name="rdb-bulk-import-type"]').attr('disabled',true);
        }else{
            $('input[name="rdb-bulk-import-type"]').attr('disabled',false);
        }
    });


    $(".on-modal-bulk-import") && $(".on-modal-bulk-import").on("click", async () => {
        const permitAccount = await isPermitAccount();
        if (permitAccount) {
            $("#modalBulkImport").modal("show");
        }
    });

    /** Hide alert Alireviews bulk importent **/
    let isShowAlertBulkImporter = true;
    const btnHideBulkImporter = document.querySelector("#bulk-info-hide");
    const bulkImporter = document.querySelector("#alireviews-bulk-importer");
    const btnAction = document.querySelector("#alireviews-bulk-nav-button");

    btnHideBulkImporter &&
    btnHideBulkImporter.addEventListener("click", function () {
        bulkImporter && (bulkImporter.style.display = "none");
        isShowAlertBulkImporter = false;
        btnAction && (btnAction.style.display = "inline-block");
    });

    $('input[name="rdb-bulk-import"]').on('change',function () {
        const productType = $(this).val();
        if(productType == 'none'){
            $('input[name="rdb-bulk-import-type"]').attr('disabled',true);
        }else{
            $('input[name="rdb-bulk-import-type"]').attr('disabled',false);
        }
    });


    $("#get-review-bulk").on("click", async e => {
        await localStorage.setItem('radioImport', radioImport)
        /*
        await Storage.setItem('rdb_bulk_import', rdb_bulk_import)
        await Storage.setItem('chk_auto_next_page', chk_auto_next_page)  */
        if (isProcessBulk) {
            console.log('another bulk process is running')
            return
        } else {
            isProcessBulk = true
            const shop = getShopifyDomain();
            const shopSetting = await api.getSetting(shop);
            if (shopSetting.status === "error") {
                const {message = "", url = ""} = shopSetting;
                const msg = `${message} <a href="${url}" target="_blank">Upgrade</a>`;
                toast(msg, "warning");
                handleHideModalBulkImport();
                $("#modalBulkImport").modal("hide");
                return;
            }
            const elProgressBar = document.querySelector(".ali-bulk-process");
            const elLifeBulkImport = elProgressBar.querySelector("#life_process_bulk");
            //elLifeBulkImport.setAttribute("aria-valuemax", l);
            elProgressBar.style.display = "block";
            const bulk_close_btn = document.querySelector("#alireviews_bulk_close_btn")
            bulk_close_btn.style.display = "none"

            moveProcessbar(0, 100);

            const elRadio = document.querySelector('input[name="rdb-bulk-import"]:checked')
            const elRadioType = document.querySelector('input[name="rdb-bulk-import-type"]:checked')
            console.log('BULK ACTION - GET REVIEWS: [2/] Option:', elRadio.value);
            const typeProduct = elRadio.value;
            localStorage.setItem("typeProduct", typeProduct);
            localStorage.setItem("radioImport", elRadio.value);
            const type = elRadioType.value === "remove" ? "del_add_new" : "false";
            localStorage.setItem("typeImport", type);
            const nextPageVal = $('input[name="rdb-next-page"]:checked').val();

            let pageCount = 0
            const pa = document.querySelector('.paginator')
            if (pa) {
                const paChild = pa.querySelectorAll('li')
                const endPageElem = paChild[paChild.length - 1].innerText ? paChild[paChild.length - 1].innerText : paChild[paChild.length - 2].innerText
                pageCount = parseInt(endPageElem)
            } else {
                pageCount = 1
            }

            var from = 1, to = pageCount
            if (nextPageVal === "one-page") {
                const pageNumber = getPageNumber()
                from = pageNumber
                to = pageNumber

                findProductsAndGetReview(from, to, typeProduct, type)
            } else {
                const limitPage = 5; // mỗi lần chỉ import 5 page
                let toInTime = 1;

                if (to <= limitPage) {
                    await findProductsAndGetReview(from, to, typeProduct, type);
                } else {
                    const totalTimeImport = Math.ceil(to / limitPage);  // số lần import
                    console.log('totalTimeImport', totalTimeImport);
                    moveProcessbar(20, 100);

                    for (let time = 0; time < totalTimeImport; time++) {
                        try {
                            if (time === 0) {
                                from = 1;
                                toInTime = limitPage;
                            } else {
                                from = time * limitPage + 1;
                                toInTime = from - 1 + limitPage;
                            }

                            if (toInTime > to) {
                                toInTime = to;
                            }
                            await findProductsAndGetReview(from, toInTime, typeProduct, type, time + 1, totalTimeImport);
                            await sleep(2000);
                        } catch (err) {
                            console.log('err.message', err.message)
                        }
                    }
                }
            }


        }
    });

    // số lần import bị emty products
    let timeHasProducts = 0;

    async function findProductsAndGetReview(from, to, typeProduct, type, currentTime = false, totalTime = false) {
        // hide modal
        handleHideModalBulkImport();
        $("#modalBulkImport").modal("hide");
        // find all product id list
        const productIdList = await findProductIdListPageNew(
            from,
            to,
            typeProduct
        );

        // Not found Product can import review
        if (productIdList === undefined || productIdList.length == 0) {
            if (currentTime) {
                if (currentTime < totalTime) {
                    // nếu import nhiều page thì việc k có products ở lần này vẫn bỏ qua đc
                    moveProcessbar(100, 100, currentTime, totalTime);
                    return;
                } else {
                    if (timeHasProducts === 0) {
                        toast(`No matching products. Please change option and try again!`, "warning");
                        $('.ali-bulk-process').hide();
                        isProcessBulk = false;
                        return;
                    } else {
                        moveProcessbar(100, 100, currentTime, totalTime);
                        showButtonFinish();
                        return;
                    }
                }
            } else {
                console.log('productIdList', productIdList);
                toast(`No matching products. Please change option and try again!`, "warning");
                $('.ali-bulk-process').hide();
                isProcessBulk = false;
                return;
            }
        }

        if (currentTime) {
            timeHasProducts++;
        }

        moveProcessbar(20, 100, currentTime, totalTime);

        console.log('BULK ACTION - GET REVIEWS: [3/] find all product id list', productIdList);
        await bulkImportProduct(productIdList, type, false, currentTime, totalTime).catch(err => {
        });
        isProcessBulk = false
    }

    async function bulkImportProduct(
        productIdList,
        type = "del_add_new",
        nextPage = false,
        currentTime = false,
        totalTime = false
    ) {
        try {
            // find shop settings
            console.log('BULK ACTION - GET REVIEWS: [4/] start run Bulk Import');
            let currentProcessLength = 21;
            const l = 79 / productIdList.length;

            // show progress bar
            const elProgressBar = document.querySelector(".ali-bulk-process");
            moveProcessbar(21, 100, currentTime, totalTime);

            const shop = getShopifyDomain();
            const shopSetting = await api.getSetting(shop);
            if (shopSetting.status === "error") {
                const {message = "", url = ""} = shopSetting;
                const msg = `${message} <a href="${url}" target="_blank">Upgrade</a>`;
                toast(msg, "warning");
                return;
            }
            const {
                data: {setting = {}}
            } = shopSetting;

            const {
                get_only_star = [4, 5],
                get_only_picture = [],
                get_only_content = [],
                get_max_number_review: max_number_review,
                except_keyword = "",
                country_get_review: review_filter_country,
                language_translate_reviews = "www",
                get_number_review_from : get_number_review_from,
                get_number_review_to : get_number_review_to,
                good_rating_percent : good_rating_percent,
                get_good_rating_star : get_good_rating_star,
                bad_rating_percent : bad_rating_percent,
                choose_import_method : choose_import_method,
            } = setting;

            let _get_only_picture, _get_only_content;
            if (typeof get_only_picture === "object") {
                if (get_only_picture.length === 1) {
                    const value = get_only_picture.pop();
                    _get_only_picture = value == "true" || value == true ? true : false;
                } else {
                    _get_only_picture = "all";
                }
            } else {
                _get_only_picture = "all";
            }
            if (typeof get_only_content === "object") {
                if (get_only_content.length == 1) {
                    const value = get_only_content.pop();
                    _get_only_content = value == "true" || value == true ? true : false;
                } else {
                    _get_only_content = "all";
                }
            } else {
                _get_only_content = "all";
            }
            console.log('BULK ACTION SETTING', setting);
            const language_translate = setting.hasOwnProperty("translate_reviews") ? (setting.translate_reviews == 1 ? true : false) : false;
            const _settings = {
                get_only_star,
                get_only_picture: _get_only_picture,
                get_only_content: _get_only_content,
                except_keyword,
                review_filter_country,
                language_translate,
                max_number_review,
                language_translate_reviews,
                get_number_review_from,
                get_number_review_to,
                good_rating_percent,
                bad_rating_percent,
                get_good_rating_star,
                choose_import_method,
            };

            console.log("BULK IMPORT [/] SETTINGS ", _settings);

            let countReview = 0;
            const savedReviews = []

            //normal
            for (var i = 0; i < productIdList.length; i++) {
                const item = productIdList[i]
                const {productShopifyId, productAliId} = item;
                const _result = `https://www.aliexpress.com/item//${productAliId}.html`
                const reviews = await getReviewFromUrlandSetting(_result, _settings);
                const payload = {reviewObj: JSON.stringify(reviews), type};
                savedReviews.push({
                    shop: shop,
                    productShopifyId: productShopifyId,
                    payload: payload,
                    reviewsLength: reviews.length
                })
                await api.saveAliUrl({shopName: shop, productId: productShopifyId, aliExpressLink: _result});
                currentProcessLength += l / 2
                moveProcessbar(currentProcessLength, 100, currentTime, totalTime)

            }
            const retryArrs = []
            for (var i = 0; i < savedReviews.length; i++) {
                const item = savedReviews[i];
                let isMoveProgress = true
                try {
                    if (item.reviewsLength) {
                        const saveReview = (await api.saveReview(item.shop, item.productShopifyId, item.payload, setting))
                        if (saveReview.status === 'success' && saveReview.result.status && countReview <= 1) {
                            countReview++
                        } else if (saveReview.status === '429') {
                            isMoveProgress = false
                            retryArrs.push(item)
                        }
                    }
                } catch (error) {
                    console.log(error)
                }
                if (isMoveProgress) {
                    currentProcessLength += l / 2
                    moveProcessbar(currentProcessLength, 100, currentTime, totalTime);
                }

                await (new Promise(resolve => {
                    setTimeout(() => {
                        resolve()
                    }, 2000);

                }))
                await Promise.all(
                    retryArrs.map(async function (item) {
                        try {
                            if (item.reviewsLength) {
                                const saveReview = (await api.saveReview(item.shop, item.productShopifyId, item.payload, setting))
                                if (saveReview.status === 'success' && saveReview.result.status && countReview <= 1) {
                                    countReview++
                                }
                            }
                        } catch (error) {
                            console.log(error)
                        }
                        currentProcessLength += l / 2
                        moveProcessbar(currentProcessLength, 100, currentTime, totalTime);
                    })
                )
            }

            let bulk_close_btn_show = true;
            if (currentTime && totalTime) {
                bulk_close_btn_show = false;
                if (currentTime >= totalTime) {
                    bulk_close_btn_show = true;
                }
            }

            if (bulk_close_btn_show) {
                showButtonFinish();
            }
            console.log("savedReviews", savedReviews)
        } catch (error) {
            throw error;
        }
    }

    function showAliBtnActionNavbar(event) {
        var btnAction = document.querySelector("#alireviews-bulk-nav-button");

        if (isShowAlertBulkImporter) {
            if (event.currentTarget.scrollY > 163) {
                btnAction.style.display = "inline-block";
            } else {
                btnAction.style.display = "none";
            }
        }
    }

    function moveProcessbar(current = 0, total = 100, currentTime = false, totalTime = false) {
        const elem = document.querySelector("#life_process_bulk");
        const elem_lbl = document.querySelector("#life_process_bulk_label");
        elem.setAttribute("aria-valuenow", current);

        let moving = (current / total * 100).toFixed(1);

        if (currentTime && totalTime) {
            const cheetProcess = 20;
            let totalNew = total - cheetProcess;
            let avgTime = totalNew / totalTime;

            if (currentTime === 1) {
                moving = ((current * avgTime / totalNew) + cheetProcess).toFixed(1);
            } else {
                moving = (((current * avgTime / totalNew) + (avgTime * (currentTime - 1))) + cheetProcess).toFixed(1);
            }

            if (moving > total) {
                moving = total;
            }

        }
        console.log('currentTime', currentTime);
        console.log('moving', moving);
        elem.style.width = `${moving}%`;
        elem_lbl.innerHTML = `${moving}/${total}%`;
    }

    window.onscroll = function (event) {
        showAliBtnActionNavbar(event);
    };

// ======================================= //
// Remove reviews product selected on current page //
// ======================================= //

    document.body.insertAdjacentHTML(
        "beforeend",
        '<div id="alireviews-remove-bulk-importer"></div>'
    );
    const alireviewsRemoveBulkImporter = document.querySelector(
        "#alireviews-remove-bulk-importer"
    );
    alireviewsRemoveBulkImporter.insertAdjacentHTML(
        "afterend",
        bulkImportRemoveModalView()
    );

// Turn on Modal
    $(".on-modal-remove-bulk-import") && $(".on-modal-remove-bulk-import").on("click", async () => {
        console.log('REMOVE ALL [1/]');
        const permitAccount = await isPermitAccount();
        if (permitAccount) {
            $("#modalRemoveBulkImport").modal("show");
        }
    });

// Remove reviews
    $("#ali-remove-review-bulk").on("click", async () => {
      $("#modalRemoveBulkImport").modal("hide");
        const oberloUrl = new URL(window.location.href);
        const oberloPage = oberloUrl.searchParams.get("page") || 1;
        const productList = await prepareProductListPageNew(true, oberloPage)
        const shop = /window.App.payload.currentShopDomain_ = "(.*?)";/m.exec(document.documentElement.textContent)[1]
        const productIdFilter = []
        await Promise.all(
            productList.map(async (product) => {

                //const productReview = (await api.checkHasReview(shop, product.ext_id));
                //if (productReview && productReview.is_reviews) {
                productIdFilter.push({productShopifyId: product.ext_id})
                //}
            })
        )

        removeReviewsProduct(productIdFilter)
            .then(result => {
                const {status = "error", message = "", url = ""} = result;
                const type = status === "error" ? "warning" : "success";
                const link = url ? `<a href="${url}" target="_blank">upgrade</a>` : "";
                const msg = link ? `${message} ${link}` : `${message}`;
                msg && toast(msg, type);

                console.log('REMOVE ALL [2/] Remove icon-tick');
                for (let icon of document.querySelectorAll('.alireview-icon')) {
                    icon.classList.remove('alireview-icon-imported');
                }
                console.log('REMOVE ALL [3/] DONE!');
            })
            .catch(err => {
                toast("Error!");
            });
    });

    async function removeReviewsProduct(productIdList) {
        if (productIdList.length) {
            try {
                const shop = getShopifyDomain();
                const result = await api.deleteReviews(shop, productIdList);
                return result;
            } catch (error) {
                throw error;
            }
        }
        return {}
    }


    function sleep(ms) {
        return new Promise(resolve => setTimeout(resolve, ms));
    }

    function showButtonFinish() {
        const elProgressBar = document.querySelector(".ali-bulk-process");

        const bulk_close_btn = document.querySelector("#alireviews_bulk_close_btn")
        bulk_close_btn.style.display = "inline-block"
        const close_bulk_progress_icon = document.querySelector('button.close-progress')
        close_bulk_progress_icon.addEventListener('click', function () {
            elProgressBar.style.display = "none";
        })
        bulk_close_btn.addEventListener('click', function () {
            elProgressBar.style.display = "none";
        })
    }
}


