import { findProductId, getShopifyDomain } from "../../oberlo/modules/helper";
import api from "../../oberlo/modules/api";

export default async function findProductIdList(from, to, type = 'all') {
  const _products = await prepareProductList(from, to); // id, link

  const productHasLink = _products.filter(item => item.link_aliexpress != null);
  const productListIDHasLink = productHasLink.map(item => item.id);
  const shop = getShopifyDomain();
  let filterResultList = []

  switch(type){
    case 'imported':
    case 'none':
      const checkProductsHasReviews = await api.checkHasReviews(shop, {product_id_list: productListIDHasLink});
      if(!checkProductsHasReviews.status){
        return filterResultList;
      }
      let productsFilterReviews = [];
      if(type == "imported"){
        productsFilterReviews = checkProductsHasReviews.result.filter(item => item.is_reviews == 1);
      }else{
        productsFilterReviews = checkProductsHasReviews.result.filter(item => item.is_reviews == 0);
      }
      // const productsHasReviews = checkProductsHasReviews.result.filter(item => item.is_reviews == 0);
      const productsHasReviewsListID = productsFilterReviews.map(item => item.id);
      const newProductHasLink = productHasLink.filter(item =>  productsHasReviewsListID.indexOf(item.id) > -1);

      for(let product of newProductHasLink) {
        filterResultList.push(product);
      }
      break;
    default:
      filterResultList = productHasLink;
  }

  console.log('filterResultList',  filterResultList)
  return filterResultList
}

async function prepareProductList(from, to) {
  let productList = [];

  for(let i = from; i <= to; i++) {
    const products = await getProductsPage(i);
      for(let product of products.data) {
        productList.push({
          id: product.id,
          link_aliexpress: product.source_product_link
        });
      }
  }

  console.log('productList', productList);
  return productList;
}

export async function getProductsPage(_page = 1) {
  let request_url = window.location.href
  let _url = new URL(request_url);
  _url.searchParams.set("page", _page);
  const result = await api.getAliodersProductPage(`${ALIORDERS}/products/all${_url.search}`);
  console.log('getProductsPage()', result);
  if(result && result !== null && typeof (result.products) != "undefined"){
    return result.products;
  }else{
    return [];
  }
}


