Api.prototype.saveReview = async function saveReview(shop, productId, data, settings = []) {
  const shopDomain = encodeURIComponent(shop);
  const url = `${this.origin}/shops/${shopDomain}/products/${productId}`;
  console.log('[saveReview] manage reviews imported', url);
  try {
    const _data = Object.assign({}, data, {source: 'aliorder'})
    const dataSave = {
      reviews: _data,
      settings: settings
    }
    const res = await put(url, dataSave);
    const { status, result } = res;
    if (status === 200 && res.ok) {
      const el = document.querySelector('#product-list-item' + productId + ' .eAlrv-icon-handle-import');
      if(el && result.totalReviews > 0) {
        el && el.classList.add('eAlrv-icon-handle-imported');
      }

      return {status:'success', result:result};
    }
    else if (status === 429) {
      return {status:'429'}
    }    
  } catch (error) {
    console.log(error)
  }
  return {status: 'failure'}
};

