export function getAlilink(id, callback) {
  console.log('getAlilink()', id);
  const url = `https://app.oberlo.com/ajax/products/ali-url?id=${id}`;
  return $.get(url, response => {
    if (response.status === 1 && response.success) {
      return callback(response);
    }
    throw new Error("Could not get aliexpress link");
  });
}

export async function promiseGetAliLink(id) {
  console.log('promiseGetAliLink()', id);
  const url = `https://app.oberlo.com/ajax/products/ali-url?id=${id}`;
  return new Promise(resolve => {
    $.get(url, response => {
      if (response.status === 1 && response.success) {
        const { url } = response;
        try {
          const urlParse = new URL(url);
          const link =
            url.includes("dl_target_url") || url.includes("ulp")
              ? urlParse.searchParams.get("dl_target_url") ||
                urlParse.searchParams.get("ulp")
              : url;
          return resolve(link);
        } catch (error) {
          return resolve("");
        }
      }
      return resolve("");
    });
  });
}



