async function prepareProductList(from, to) {
  // calculate number of page
  /*const pa = document.querySelector('.pagination')
  const count = pa ? (pa.childElementCount - 2) : 0*/

  let request_url = window.location.href
  let _url = new URL(request_url);
  var productList = []
  for (let i = from; i <= to; i++) {
    console.log('prepareProductList page', i)
    _url.searchParams.set("page", i);
    const result = await api.collectProductFromOberloPage(`${URLMYPRODUCTSOBERLO}${_url.search}`)
    //console.log("fdsfafsafsda", result)
    if (result) {
      const regex = /window.App.payload.products = (.*?);$/m
      const rawResult = regex.exec(result)[1]
      
      //console.log(rawResult)
      
      try {
        const dataJson = JSON.parse(rawResult)
        if (dataJson !== undefined) {
          productList = productList.concat(dataJson)
        }
      }
      catch (error) {
        console.log(error)
      }
    }
  }
  //console.log(JSON.stringify(productList[18]), JSON.stringify(productList[19]))
  return productList;
}
async function pauseUltil(delay){  
  return new Promise(resolve=>{
    console.log('start set timeout')
    setTimeout(() => {
      console.log('end set time out')
      resolve()      
    }, delay);
  })
}




export async function findProductIdListPageNew(from, to, type = 'all') {
  // declare list el

  const _products = await prepareProductListPageNew(false, 1, from, to)

  console.log('start findProductIdList ', _products)

  const shop = getShopifyDomain();
  console.log("shop domain", shop)
  const filterResultList = []

  //check has review?

  switch(type){
    case 'imported':
    case 'none':
      const prepareList = []
      _products.forEach(function(product){
        if (product.product.gone_since === null && product.product.source != 1) {
          product.productAliId = product.product.ext_id
          product.productAliLink = product.product.url
          prepareList.push(product.ext_id)
        }
      })
      const checkResult = await api.checkHasReviews(shop, {product_id_list:prepareList})
      if(!checkResult.status){
        return filterResultList;
      }
      _products.forEach(function(product){
        checkResult.result.forEach(function (review){
          if (review.id == product.ext_id){
            if(type == "imported"){
              if (review.is_reviews == '1') {
                filterResultList.push({productShopifyId: product.ext_id, productAliId: product.productAliId})
              }
            }
            else{
              if (review.is_reviews != '1') {
                filterResultList.push({productShopifyId: product.ext_id, productAliId: product.productAliId})
              }
            }
          }
        })
        if (product.product.gone_since === null && product.product.source != 1) {
          product.productAliId = product.product.ext_id
          product.productAliLink = product.product.url
          prepareList.push(product.ext_id)
        }
      })
      break;
    default:
      _products.forEach(function(product){
        if (product.product.gone_since === null && product.product.source != 1) {
          product.productAliId = product.product.ext_id
          product.productAliLink = product.product.url
          filterResultList.push({ productShopifyId: product.ext_id, productAliId: product.productAliId })
        }
      })
  }

  return filterResultList;
}




