let isProcessBulk = false  
let isShowAlertBulkImporter = true ;

export async function renderActionProductAllPages() {
  console.log('ALIORDERS BULKIMPORT [1/] Render Bulk Import Wrap');
  const shop = getShopifyDomain();
  const urlSettings = `https://${shop}/admin/apps/${APP_NAME}/import-settings`
  
  const el_HeaderTop = document.querySelector('.header-top .shopify-name');
  el_HeaderTop.insertAdjacentHTML("afterbegin", '<li id="alireviews-bulk-nav-button"></li>');

  const el_BulkImportButton = document.getElementById('alireviews-bulk-nav-button');
  el_BulkImportButton.innerHTML = bulkNavButton({ origin: ORIGIN });

  document.body.insertAdjacentHTML("beforeend", progressView())

  const el_BulkImportWrap = document.querySelector('.ao-header-page-action');
  el_BulkImportWrap && el_BulkImportWrap.insertAdjacentHTML("beforeend", bulkImportHeroView({ origin: ORIGIN }));
  el_BulkImportWrap && el_BulkImportWrap.insertAdjacentHTML("beforeend", bulkImportModalView({ origin: urlSettings }));

  window.onscroll = function (event) {
    showAliBtnActionNavbar(event);
  };
  
  openModalBulkImport();
}

async function openModalBulkImport() {
  $(".on-modal-bulk-import") && $(".on-modal-bulk-import").on("click", async function() {
    console.log('ALIORDERS BULKIMPORT [] Click open modal');

    if(isProcessBulk == false) {
      $('.ali-bulk-process').hide();
      const isSameShop = await isAliordersAndAlireviewsSameShop();
      isSameShop && $("#modalBulkImport").modal("show");
    }
  });

  let radioImport = null
  $("#modalBulkImport").on("show.bs.modal", async () => {
    radioImport = await localStorage.getItem("radioImport");
    console.log("BULK ACTION - GET REVIEWS: [1/] Show modal #modalBulkImport", radioImport)
    $(`.group-checkbox input[value="${radioImport}"]`).attr("checked", true);
      if(radioImport == 'none'){
          $('input[name="rdb-bulk-import-type"]').attr('disabled',true);
          $('.eAlrv-radio-wrap-affected').addClass('eAlrv-blocked');
      }else{
          $('input[name="rdb-bulk-import-type"]').attr('disabled',false);
          $('.eAlrv-radio-wrap-affected').removeClass('eAlrv-blocked');
      }

    const infoShop = await api.infoShop(getShopifyDomain());
    const { app_plan = "" } = infoShop.result;
    
    if(app_plan != "" && app_plan != "free") {
      $('#modalBulkImport .eAlrv-no-free.eAlrv-radio-wrap').removeClass('eAlrv-blocked');
      $('#modalBulkImport .eAlrv-no-free.eAlrv-tooltip').hide();
    }
  });

  /** Hide alert Alireviews bulk importent **/
  const btnHideBulkImporter = document.querySelector("#bulk-info-hide");
  const bulkImporter = document.querySelector("#alireviews-bulk-importer");
  const btnAction = document.querySelector("#alireviews-bulk-nav-button");

  btnHideBulkImporter && btnHideBulkImporter.addEventListener("click", function () {
    bulkImporter && (bulkImporter.style.display = "none");
    isShowAlertBulkImporter = false;
    btnAction && (btnAction.style.display = "inline-block");
  });

    $('input[name="rdb-bulk-import"]').on('change',function () {
        const productType = $(this).val();
        if(productType == 'none'){
            $('input[name="rdb-bulk-import-type"]').attr('disabled',true);
            $('.eAlrv-radio-wrap-affected').addClass('eAlrv-blocked');
        }else{
            $('input[name="rdb-bulk-import-type"]').attr('disabled',false);
            $('.eAlrv-radio-wrap-affected').removeClass('eAlrv-blocked');
        }
    });

  $("#get-review-bulk").on("click", async e => {
    const infoShop = await api.infoShop(getShopifyDomain());
    const { app_plan = "", url_pricing = "" } = infoShop.result;
    
    if(app_plan != "" && app_plan == "free") {
      $('#modalBulkImport .eAlrv-no-free.eAlrv-radio-wrap').addClass('eAlrv-blocked');
      $('#modalBulkImport .eAlrv-no-free.eAlrv-tooltip').show();

      if($("input[name='rdb-bulk-import']:checked").val() == "keep" || $("input[name='rdb-next-page']:checked").val() == "all-page") {
        toast(`Only available in paid plans <a href="${url_pricing}" target="_blank">Upgrade</a>`, "warning");
        return false;
      }
    }

    await localStorage.setItem('radioImport', radioImport)

    if (isProcessBulk) {
      console.log('Another bulk process is running')
      return
    }
    else {
      isProcessBulk = true
      
      const elProgressBar = document.querySelector(".ali-bulk-process");
      // const elLifeBulkImport = elProgressBar.querySelector("#life_process_bulk");
      elProgressBar && (elProgressBar.style.display = "block");

      const bulk_close_btn = document.querySelector("#alireviews_bulk_close_btn")
      bulk_close_btn && (bulk_close_btn.style.display = "none")

      moveProcessbar(0, 100);

      const elRadio = document.querySelector('input[name="rdb-bulk-import"]:checked')
      const elRadioType = document.querySelector('input[name="rdb-bulk-import-type"]:checked')
      console.log('BULK ACTION - GET REVIEWS: [2/] Option:', elRadio.value);
      const typeProduct = elRadio.value;
      localStorage.setItem("typeProduct", typeProduct);
      localStorage.setItem("radioImport", elRadio.value);
      const type = elRadioType.value === "remove" ? "del_add_new" : "false";
      localStorage.setItem("typeImport", type);
      const nextPageVal = $('input[name="rdb-next-page"]:checked').val();
      

      let from = 1;
      let to = 1;

      // var from = 1, to = pageCount
      if (nextPageVal === "one-page") {
          const { href = '' } = document.location
          const pageUrl = new URL(href)
          const searchParams = pageUrl.searchParams
          const pageNumber = searchParams.get('page') || 1
          from = pageNumber;
          to = pageNumber;

          // hide modal
          $("#modalBulkImport").modal("hide");

          await findProductsAndGetReview(from,to,typeProduct,type);
      }else{
          const infoProducts = await getProductsPage();
          const last_page = infoProducts.last_page; // tổng page của aliorder
          const limitPage = 5; // mỗi lần chỉ import 5 page

          // hide modal
          $("#modalBulkImport").modal("hide");

          if (last_page <= limitPage){
              to = last_page;
              await findProductsAndGetReview(from,to,typeProduct,type);
          }else{
              const totalTimeImport = Math.ceil(last_page / limitPage);  // số lần import
              moveProcessbar(20, 100);

              for (let time = 0; time < totalTimeImport; time++){
                  try {
                      if (time === 0){
                          from = 1;
                          to = limitPage;
                      }else{
                          from = time *  limitPage + 1;
                          to = from - 1 + limitPage;
                      }

                      if (to > last_page){
                          to = last_page;
                      }
                      await findProductsAndGetReview(from,to,typeProduct,type,time + 1,totalTimeImport);
                      await sleep(2000);
                  }
                  catch(err) {
                      console.log('err.message',err.message)
                  }
              }
          }
      }
    }
  });
}

// số lần import bị emty products
let timeHasProducts = 0;
async function findProductsAndGetReview(from,to, typeProduct, type,currentTime = false, totalTime = false){
    // find all product id list
    const productIdList = await findProductIdList(from, to, typeProduct);

    // Not found Product can import review
    if(productIdList === undefined || productIdList.length === 0) {
        if (currentTime){
            if(currentTime < totalTime ){
                // nếu import nhiều page thì việc k có products ở lần này vẫn bỏ qua đc
                moveProcessbar(100, 100, currentTime,totalTime);
                return;
            }else{
                if (timeHasProducts === 0){
                    toast(`No matching products. Please change option and try again!`, "warning");
                    $('.ali-bulk-process').hide();
                    isProcessBulk = false;
                    return;
                }else {
                    moveProcessbar(100, 100, currentTime,totalTime);
                    showButtonFinish();
                    return ;
                }
            }
        }else{
            console.log('productIdList', productIdList);
            toast(`No matching products. Please change option and try again!`, "warning");
            $('.ali-bulk-process').hide();
            isProcessBulk = false;
            return;
        }
    }
    if (currentTime){
        timeHasProducts ++;
    }

    moveProcessbar(20, 100, currentTime,totalTime);

    console.log('BULK ACTION - GET REVIEWS: [3/] find all product id list', productIdList);
    await bulkImportProduct(productIdList, type, false, currentTime,totalTime).catch(err => { });
    isProcessBulk = false
}

 
