    const l = 79 / productIdList.length;

    // show progress bar
    moveProcessbar(21, 100, currentTime,totalTime);

    const shop = getShopifyDomain();
    const shopSetting = await api.getSetting(shop);
    console.log('shopSetting', shopSetting);
    if (shopSetting.status === "error") {
      const { message = "", url = "" } = shopSetting;
      const msg = `${message} <a href="${url}" target="_blank">Upgrade</a>`;
      toast(msg, "warning");
      return;
    }
    const {
      data: { setting = {} }
    } = shopSetting;

    const {
      get_only_star = [4, 5],
      get_only_picture = [],
      get_only_content = [],
      get_max_number_review: max_number_review,
      except_keyword = "",
      country_get_review: review_filter_country,
      language_translate_reviews = "www",
        get_number_review_from : get_number_review_from,
        get_number_review_to : get_number_review_to,
        good_rating_percent : good_rating_percent,
        get_good_rating_star : get_good_rating_star,
        bad_rating_percent : bad_rating_percent,
        choose_import_method : choose_import_method,
    } = setting;

    let _get_only_picture, _get_only_content;
    if (typeof get_only_picture === "object") {
      if (get_only_picture.length === 1) {
        const value = get_only_picture.pop();
        _get_only_picture = value == "true" || value == true ? true : false;
      } else {
        _get_only_picture = "all";
      }
    } else {
      _get_only_picture = "all";
    }
    if (typeof get_only_content === "object") {
      if (get_only_content.length == 1) {
        const value = get_only_content.pop();
        _get_only_content = value == "true" || value == true ? true : false;
      } else {
        _get_only_content = "all";
      }
    } else {
      _get_only_content = "all";
    }
    console.log('BULK ACTION SETTING', setting);
    const language_translate = setting.hasOwnProperty("translate_reviews") ? (setting.translate_reviews == 1 ? true : false) : false;
    const _settings = {
      get_only_star,
      get_only_picture: _get_only_picture,
      get_only_content: _get_only_content,
      except_keyword,
      review_filter_country,
      language_translate,
      max_number_review,
      language_translate_reviews,
        get_number_review_from,
        get_number_review_to,
        good_rating_percent,
        bad_rating_percent,
        get_good_rating_star,
        choose_import_method,
      is_bulk_import_action: true
    };

    console.log("BULK IMPORT [/] SETTINGS ", _settings);

    let countReview = 0;
    const savedReviews = []
    
    //normal
    for (var i = 0; i < productIdList.length; i++){
      const item = productIdList[i]
      console.log('item', item);
      // const { productShopifyId, productAliId } = item;
      // const _result = `https://www.aliexpress.com/item//${productAliId}.html`
      const _result = item.link_aliexpress;
      const reviews = await getReviewFromUrlandSetting(_result, _settings);
      const payload = { reviewObj: JSON.stringify(reviews), type };        
      savedReviews.push({ shop: shop, productShopifyId: item.id, payload: payload, reviewsLength: reviews.length })
      await api.saveAliUrl({shopName: shop, productId: item.id, aliExpressLink: _result});
      currentProcessLength += l / 2
      moveProcessbar(currentProcessLength, 100, currentTime,totalTime)
    }

      const retryArrs = []
      for (var i = 0; i < savedReviews.length; i++) {
          const item = savedReviews[i];
          console.log('itemitemitem',item);
          let isMoveProgress = true
          try {
              if (item.reviewsLength) {
                  const saveReview = (await api.saveReview(item.shop, item.productShopifyId, item.payload, setting))
                  if (saveReview.status === 'success' && saveReview.result.status && countReview <= 1) {
                      countReview++
                  } else if (saveReview.status === '429') {
                      isMoveProgress = false
                      retryArrs.push(item)
                  }
              }
          } catch (error) {
              console.log(error)
          }
          if (isMoveProgress) {
              currentProcessLength += l / 2
              moveProcessbar(currentProcessLength, 100, currentTime, totalTime);
          }

          await (new Promise(resolve => {
              setTimeout(() => {
                  resolve()
              }, 2000);

          }))
          await Promise.all(
              retryArrs.map(async function (item) {
                  try {
                      if (item.reviewsLength) {
                          const saveReview = (await api.saveReview(item.shop, item.productShopifyId, item.payload, setting))
                          if (saveReview.status === 'success' && saveReview.result.status && countReview <= 1) {
                              countReview++
                          }
                      }
                  } catch (error) {
                      console.log(error)
                  }
                  currentProcessLength += l / 2
                  moveProcessbar(currentProcessLength, 100, currentTime, totalTime);
              })
          )
      }

    let bulk_close_btn_show = true;
    if(currentTime && totalTime){
        bulk_close_btn_show = false;
        if(currentTime >= totalTime){
            bulk_close_btn_show = true;
        }
    }

    if (bulk_close_btn_show){
        showButtonFinish();
    }

    console.log("savedReviews ", savedReviews)

    // Thay icon san pham da co review
