import viewModalSettingReview from "../views/modalSettingReview.hbs";
import viewModalSettingReviewOld from "../views/modalSettingReviewOld.hbs";
import { idModalSettingReview } from "./constant";

function clearAllModalelModalSetting() {
  const elModalSetting = $("#idModalSettingReview");
  elModalSetting.length && elModalSetting.remove();
  const elModalBackdrop = $(".modal-backdrop");
  elModalBackdrop.length && elModalBackdrop.remove();
}

export default function modalSettingReview(data) {
  console.log('HANDLER [] Fill data from GeneralSetting to modal', data);
  clearAllModalelModalSetting();
  const alireviewModalWrapper = document.querySelector(
    ".alireview-modal-wrapper"
  );
  let versionApp = 5; // app version 5.0
  if(data.choose_import_method === false){
    versionApp = 4;
  }

  if(versionApp === 5){
    alireviewModalWrapper.innerHTML = viewModalSettingReview(data);
  }else{
    alireviewModalWrapper.innerHTML = viewModalSettingReviewOld(data);
  }
  $(idModalSettingReview).modal({ show: true, backdrop: "static" });

  // $("#get-review-filter-country").multiselect({
  //   nonSelectedText: "Choose Language",
  //   buttonContainer: '<div class="selected-parents-container"></div>',
  //   enableFiltering: true,
  //   includeSelectAllOption: true,
  //   enableCaseInsensitiveFiltering: true,
  //   onChange: function(option, checked) {},
  //   onSelectAll: function() {},
  //   onDeselectAll: function() {}
  // });
  // console.log('#5555');

  $("#get-review-filter-country").multiselect({
    nonSelectedText: "Select Location",
    enableFiltering: false,
    buttonContainer: '<div class="selected-parents-container"></div>',
    includeSelectAllOption: true,
    enableCaseInsensitiveFiltering: true,
    templates: {
      li: `
      <li class="checkList">
        <label class="eAlrv-checkbox-wrap w-100">
          <input type="checkbox" checked="checked">
          <span class="eAlrv-checkbox__checkmark"></span>
        </label>
      </li>`,
      filter: `
      <li class="multiselect-item filter">
        <div class="input-group w-100 par-10">
          <input class="form-control multiselect-search" type="text">
        </div>
      </li>`,
      filterClearBtn: false
    }
  });

  $("#get-review-filter-country + div .multiselect-container .eAlrv-checkbox-wrap").each(function(index) {
    $(this).append('<span class="eAlrv-checkbox__checkmark"></span>');
    $(this).click(function(e) {
      e.stopPropagation();
    });
    }
  );


  function formatState(state) {
    let $state = null;
    if(state.element) {
        $state = $(`<span title="${state.text}">${state.text} <img src="${$(state.element.outerHTML).attr('data-src')}"></img> ${$(state.element.outerHTML).attr('text_after')}</span>`);
    }
    return $state;
  }

  $('.js-select2').select2({ minimumResultsForSearch: Infinity, theme: "default alireview-ext-theme" });
  $('.js-select2-icon-rating').select2({
    templateResult: formatState,
    minimumResultsForSearch: Infinity,
    templateSelection: formatState,
    theme: "default alireview-ext-theme"
  });

  // $('#chooseImportMethod').on('select2:select', function (e) {
  //   let selected = e.target.value;
  //   if (selected == 0) {
  //     $('.js-setting-random-quantity').show();
  //     $('.js-setting-fixed-quantity').hide();
  //   } else {
  //     $('.js-setting-random-quantity').hide();
  //     $('.js-setting-fixed-quantity').show();
  //   }
  // });
}




