import Api from "./api";
import handlerAlireview from "./handlerAlireview";
import { getSettingsDomain, isAliordersAndAlireviewsSameShop, closeBulkProcess,checkImportReview } from "./helper";

function getReviewFromAliorders() {
    const settingsDomain = getSettingsDomain();
    const productListItems = document.querySelectorAll('.pricing-rule-block-count');
    console.log('productListItems', productListItems);
    
    // Create wrap modal 
    document.body.insertAdjacentHTML("beforeend", '<div class="alireview-modal-wrapper"></div>');
    document.body.insertAdjacentHTML("beforeend", '<div class="alireview-toast-wrapper"></div>');
    document.body.insertAdjacentHTML("beforeend", `<input type="hidden" id="isAliordersPage" value="${settingsDomain.shop}"></div>`);
    
    // 1. ALIORDERS HANDLE
    (productListItems.length > 0) && initHandleImport(settingsDomain, productListItems);
}

async function initHandleImport(_settingsDomain, _productListItems) {
    console.log('ALIORDERS HANDLE [1/] start render List Action');
    
    const shop = _settingsDomain.shop;
    const productIdList = Array.prototype.map.call(_productListItems, (productItem) => {
      const titleProduct = $(productItem).find('.title-product a')
      const productLink = titleProduct.attr("href");
      const url = new URL(productLink);
      const { pathname } = url;
      const productId = pathname.replace("/admin/products/", "");
      $(productItem).attr("data-id", productId)
      return productId;
    });
    const checkResults = await Api.checkHasReviews(shop, {product_id_list: productIdList})
    console.log('checkResults', checkResults);
    if(checkResults.status === true) {
        for(const checkResult of checkResults.result) {
            renderActionForProduct(checkResult);
        }
    }

    HandleImportReview(shop);
    console.log('ALIORDERS HANDLE [2/] render List Action DONE!');
}

function HandleImportReview(_shop) {
    const elHandleImportReviews = document.querySelectorAll('.js-alrvHandleImportReview');
    for(let i = 0; i < elHandleImportReviews.length; i++) {
        elHandleImportReviews[i].addEventListener('click', async function() {
            closeBulkProcess();
            const isSameShop = await isAliordersAndAlireviewsSameShop();
            if(isSameShop){
                const productId = elHandleImportReviews[i].getAttribute('data-id');
                const checkLimit = await checkImportReview(productId);
                console.log('checkLimit',checkLimit);
                if (checkLimit == true) {
                    handlerAlireview(elHandleImportReviews[i]);
                }
            }
        });
    }

    const elHandleManageReview = document.querySelectorAll('.js-alrvHandleManageReview');
    for(let i = 0; i < elHandleManageReview.length; i++) {
        elHandleManageReview[i].addEventListener('click', async function(event) {
            const urlManageReview = $(this).attr('url');
            closeBulkProcess();
            const isSameShop = await isAliordersAndAlireviewsSameShop();
            isSameShop && window.open(urlManageReview, '_blank');
        });
    }
}

function renderActionForProduct(checkResult) {
    const el_listAction = document.querySelectorAll(`.pricing-rule-block-count[data-id="${checkResult.id}"] .col-pricing-rules:last-child`)[0];
    const isProduct_hasLinkAliexpress = document.querySelector(`.pricing-rule-block-count[data-id="${checkResult.id}"] a.ao-text-link`);

    if(isProduct_hasLinkAliexpress != null) {
        const el_liAction = `
            <div class="eAlrv-icon-handle-import-wrap">
                <div class="eAlrv-icon-handle-item">
                    <div class="dropdown">
                        <span class="eAlrv-icon-handle-import ${checkResult.is_reviews == 1 ? 'eAlrv-icon-handle-imported' : '' }" data-toggle="dropdown"></span>

                        <div class="dropdown-menu">
                            <ul>
                                <li data-id="${checkResult.id}" class="js-alrvHandleImportReview"><a>Import reviews</a></li>
                                <li data-id="${checkResult.id}" class="js-alrvHandleManageReview" url="${checkResult.url_manage_review}"><a target="_blank">Manage reviews</a></li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        `;
        el_listAction.classList.add("eAlrv-has-icon-import-wrap")
        el_listAction.insertAdjacentHTML("beforeend", el_liAction)
    }
}

export { getReviewFromAliorders }


