function addError(arrErrors, message) {
  if(arrErrors.indexOf(message) < 0) {
    arrErrors.push(message);
  }
}

function removeError(arrErrors, message) {
  if(arrErrors.indexOf(message) > -1) {
    arrErrors.splice(arrErrors.indexOf(message), 1);
  }
}

export default async function stepShowModalSetting(productId, settings, app_plan = '') {
  console.log("HANDLER [2/] Opened modal");

  let isHasReview = false;
  const shop = getShopifyDomain();
  const resultCheckHasReview = await Api.checkHasReview(shop, productId);
  console.log('resultCheckHasReview', resultCheckHasReview);

  if(resultCheckHasReview.is_reviews == 1) {
    isHasReview = true;
  }
  
  Api.getCountries()
    .then(countries => {
      const {
        setting: {
          get_max_number_review,
          get_number_review_from = 50,
          get_number_review_to = 200,
          choose_import_method = false,
          good_rating_percent = 90,
          bad_rating_percent = 10,
          get_good_rating_star = 4,
          get_only_content = [],
          get_only_picture = [],
          get_only_star = ["1", "2", "3", "4", "5"],
          country_get_review,
          except_keyword = "",
          translate_reviews = "",
          language_translate_reviews = "en",
          is_local_name,
          is_review_status,
          import_reviews_in_date_range,
          male_name_percent,
          female_name_percent,
          auto_convert_reviewer
        },
        ali_languages_site = {
          ar: "Arabic",
          de: "German",
          en: "English",
          es: "Spanish",
          fr: "French",
          he: "Hebrew",
          it: "Italian",
          ja: "Japanese",
          ko: "Korean",
          nl: "Dutch",
          pt: "Portuguese",
          ru: "Russian",
          th: "Thai",
          tr: "Turkish",
          vi: "Vietnamese"
        },
      } = settings;
      let checked_all_picture = (get_only_picture.length == 2 || get_only_picture.length == 0) ? true : false;
      let checked_all_content = (get_only_content.length == 2 || get_only_content.length == 0) ? true : false;

      const language_translate_reviews_name = ali_languages_site[language_translate_reviews];
      let arr_plan_limit_advance_settings = ['free', 'startup', 'starter'];
      let is_limit_advance_settings = arr_plan_limit_advance_settings.includes(app_plan)
      let max_import_per_plan = `${limitNumberImport[app_plan]}`

      modalSettingReview({
        get_max_number_review,
        get_number_review_from,
        get_number_review_to,
        choose_import_method,
        good_rating_percent,
        bad_rating_percent,
        get_good_rating_star,
        get_only_content,
        get_only_picture,
        get_only_star,
        country_get_review,
        countries,
        except_keyword,
        translate_reviews,
        language_translate_reviews,
        language_translate_reviews_name,
        ali_languages_site,
        is_local_name,
        is_review_status,
        import_reviews_in_date_range,
        male_name_percent,
        female_name_percent,
        isHasReview,
        checked_all_picture,
        checked_all_content,
        is_limit_advance_settings,
        app_plan,
        max_import_per_plan,
        auto_convert_reviewer
      });

      $('#modalSettingReview').on("shown.bs.modal", event => {
        !document.body.classList.contains("modal-open") && document.body.classList.add("modal-open");
        const el = $('#modalSettingReview');

        handlerShownModalSetting(el, productId);
      });
    })
    .catch(error => {
      toast(error.message);
      throw error;
    });
}

