import {
//   findProductId,
  getShopifyDomain,
//   shouldShowModalOverwriteReview,
//   findProductTitle,
//   findProductImage,
  findAliLink
//   isPermitAccount
} from "./helper";

// import setShowModalOverwrite from "./stepShowModalOverwrite";
import stepShowModalSetting from "./stepShowModalSetting";
import stepCheckAccount from "./stepCheckAccount";
import toast from "../../../oberlo/modules/showToast";
// import showModalNotPermit from "./showModalNotPermit";

export default async function handlerAlireview(el) {
  console.log('HANDLER [1/] Start opening modal', el);
  
  const shop = getShopifyDomain();
  const productId = el.getAttribute('data-id');
  await findAliLink(el);

  stepCheckAccount(shop).then(result => {
    const { status, message = "", url = "" } = result;
    
    if (status === "success") {
      const { setting } = result;
      
      console.log('HANDLER - GENERAL SETTINGS', result.setting.setting);
      let app_plan = result.setting.app_plan || '';
      stepShowModalSetting(productId, setting, app_plan);
    }else{
      const msg = `${message} <a href="${url}" target="_blank" style="">Upgrade now</a>`;
      toast(msg, "warning");
      return false;
    }
  });
}


